* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: black;
}
body {
  background-color: #e7f6ff;
}

.wrapper {
  display: block;
  width: 80%;
  margin: 85px auto;
  padding: 45px 60px;
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
}

.line {
  height: 1px;
  border-bottom: 1px solid rgb(199, 194, 194);
  margin-bottom: 30px;
}

@media (max-width: 991px) {
  .wrapper {
    width: 80%;
    margin: 60px auto;
    padding: 35px 40px;
  }
}

@media (max-width: 767px) {
  .wrapper {
    width: 90%;
    margin: 40px auto;
    padding: 25px 30px;
  }
  .line {
    margin-bottom: 20px;
  }
}

@media (max-width: 575px) {
  .wrapper {
    margin: 20px auto;
    padding: 10px 10px;
  }
  .line {
    margin-bottom: 10px;
  }
}
